ости и попытке переживания красоты без давления личности. Работа написана с уклоном в лоу-фай хаус, остальные две части будут звучать в духе IDM и эмбиента. При записи треков Костя использовал звуки, собранные им «в полях», звуки местного быта и природы, прошедшие эйсид-конвертацию через басовый синтезатор Roland TB-03. ")]),_c('p',[_vm._v(" «Мимоза» – не просто саундтрек для декораций мира, она меняет взгляд на них вместе с переживанием простой, не затронутой человеческим смыслом красоты, которая возникает где-то между ломаным битом, реверберациями «роланда» и лучом солнца, пробивающимся между ставен деревенского домика. ")])]),_c('div',{staticClass:"col-md-6 offset-md-2"},[_c('iframe',{staticStyle:{"border":"0","width":"100%","height":"120px"},attrs:{"src":"https://bandcamp.com/EmbeddedPlayer/album=2532475065/size=large/bgcol=333333/linkcol=0f91ff/tracklist=false/artwork=small/transparent=true/","seamless":""}},[_c('a',{attrs:{"href":"https://mestnost.bandcamp.com/album/mimoza"}},[_vm._v("Mimoza by Kokokei")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-4 mt-5"},[_c('img',{attrs:{"src":"static/mimoza_cassette_1.jpg","alt":"mimoza cassette 1","width":"90%","height":"auto"}})]),_c('div',{staticClass:"col-md-4 mt-5"},[_c('img',{attrs:{"src":"/static/mimoza_cassette_2.jpg","alt":"mimoza cassette 2","width":"90%","height":"auto"}})]),_c('div',{staticClass:"col-md-4 mt-5"},[_c('img',{attrs:{"src":"/static/mimoza_cassette_3.jpg","alt":"mimoza cassette 3","width":"90%","height":"auto"}})])])}]

